body {
    margin: 0;
    background-color: hsl(218, 28%, 13%);
    ul {
        margin: 0;
        padding: 0;
        li {
            padding: 0;
            margin: 0;
        }
    }
    h1,
    h2,
    h3,
    h4,
    h5,
    h6,
    p {
        margin: 0;
    }
    a {
        text-decoration: none;
    }
}